























































@import '~@/variables';

@keyframes slideInFromLeft {
  from {
    opacity: 0;
    transform: translateX(-80px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInFromRight {
  from {
    opacity: 0;
    transform: translateX(80px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

header {
  background: url('../assets/AdobeStock_408345976_Preview.jpeg');
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &>div:last-of-type {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

@media (min-width: 768px) {
  header {
    min-height: 500px;
  }
}

@media (min-width: 1000px) {
  header {
    min-height: 640px;
  }
}

.sticky-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 10;
  padding: 1rem;
  background-color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  transition: all 250ms ease-in-out;
}

.header-content {
  position: absolute;
  z-index: 1;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-top: calc(50px + 2rem);
}

.outer-logo {
  display: flex;
  align-items: flex-end;

  span {
    display: none;
    margin-bottom: 8px;
    margin-left: 0.5rem;
    font-size: 0.75rem;
    color: #858585;
  }
}

@media (min-width: 480px) {
  .outer-logo {
    span {
      display: inline;
    }
  }
}

.logo {
  height: 50px;
  display: block;
  transition: all 250ms ease-in-out;

  path {
    fill: $ops-color-1;
  }
}

:root.has-scroll .sticky-header {
  position: fixed;
  box-shadow: 0 0 0 1px #b2b2b2;
  padding: 0.5rem 1rem;

  .logo {
    height: 40px;
  }
}

.text-right {
  text-align: right;
}

h1 {
  display: inline-block;
  text-align: left;
  color: #fff;
  margin: 0 1rem 1.5rem;

  span {
    background-color: $ops-color-1;
    padding: 0.5rem 0.75rem;
    display: inline-block;

    &:first-of-type {
      margin-bottom: 0.25rem;
    }
  }

  .line-1 {
    animation-name: slideInFromLeft;
    animation-delay: 200ms;
    animation-duration: 900ms;
    animation-fill-mode: forwards;
    animation-timing-function: cubic-bezier(0.33, 1, 0.68, 1);
    opacity: 0;
  }

  .line-2 {
    animation-name: slideInFromRight;
    animation-delay: 200ms;
    animation-duration: 900ms;
    animation-fill-mode: forwards;
    animation-timing-function: cubic-bezier(0.33, 1, 0.68, 1);
    opacity: 0;
    font-weight: 100;
    background-color: $ops-color-2;
    font-size: 0.9em;
    padding: 0.25rem 0.75rem;
  }
}

.button {
  display: inline-block;
  background-color: #A6D50A;
  color: #fff;
  padding: 0.5rem 1rem;
  font-size: 1.3rem;
  font-weight: bold;
}

ul {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;

  a {
    display: block;
    color: $ops-color-1;
  }

  i {
    margin-right: 0.25rem;
  }

  small {
    font-size: 0.7rem;
    line-height: 1.15;
  }
}

.upper-bar,
.lower-bar {
  position: absolute;
  width: 100%;
  bottom: 0;

  div {
    position: absolute;
    width: calc(50% - 20px);
    height: 100%;
    display: block;
    content: '';
    flex: 1;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      width: 0;
      height: 0;
      border-top-style: solid;
      border-top-color: transparent;
      border-bottom: 0 solid transparent;
    }

    &:first-of-type {
      left: 0;
      top: 0;

      &:after {
        left: 100%;
        border-left-style: solid;
      }
    }

    &:last-of-type {
      right: 0;
      top: 0;

      &:after {
        right: 100%;
        border-right-style: solid;
      }
    }
  }
}

.upper-bar {
  height: 25px;

  div {
    width: calc(50% - 17px);
    background-color: #ccc;

    &:after {
      border-top-width: 25px;
    }

    &:first-of-type {
      &:after {
        border-left-width: 25px;
        border-left-color: #ccc;
      }
    }

    &:last-of-type {
      right: 0;
      top: 0;

      &:after {
        right: 100%;
        border-right-width: 25px;
        border-right-color: #ccc;
      }
    }
  }
}

.lower-bar {
  height: 20px;

  div {
    width: calc(50% - 20px);
    background-color: #fff;

    &:after {
      border-top-width: 20px;
    }

    &:first-of-type {
      &:after {
        border-left-width: 20px;
        border-left-color: #fff;
      }
    }

    &:last-of-type {
      &:after {
        border-right-width: 20px;
        border-right-color: #fff;
      }
    }
  }
}
