





























































































@import '~@/variables';

input[type="checkbox"] {
  display: none;
  visibility: hidden;
  opacity: 0;
}

.contact-container {
  display: flex;
  flex-direction: column;

  fieldset {
    flex: 1;
    border: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  textarea {
    min-height: 8rem;
  }
}

@media (min-width: 768px) {
  .contact-container {
    flex-direction: row;

    .field-col-1 {
      margin-right: 0.5rem;
    }

    .field-col-2 {
      margin-left: 0.5rem;

    }

    textarea {
      flex: 1;
      min-height: auto;
    }
  }
}
