











































@import '~@/variables';

footer {
  background-color: $ops-color-1;
  color: #fff;
  min-height: 400px;
  padding: 2.5rem 1rem;

  a {
    color: #fff;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .logo {
    height: 35px;
  }

  .copy-info {
    font-size: 0.8rem;
  }
}

.container {
  display: flex;
  flex-direction: column;

  &>div {
    margin-bottom: 2.5rem;

    b {
      display: block;
      text-transform: uppercase;
      margin-bottom: 0.5rem;
    }

    &:last-of-type {
      margin-right: 0;
      flex: 1;
      text-align: right;
    }
  }
}

@media (min-width: 768px) {
  .container {
    flex-direction: row;

    &>div {
      margin-right: 3.5rem;
      margin-bottom: 0;
    }
  }
}
