@import 'variables';
@import 'helper';

html, body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  position: relative;
  color: $body-font-color;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
  background-color: #fff;
}

* {
  box-sizing: border-box;
}

h1 {
  font-size: 1.55em;
}

h2 {
  font-size: 1.35em;
}

h3 {
  font-size: 1.1em;
}

@media (min-width: 480px) {
  h1 {
    font-size: 1.7em;
  }

  h2 {
    font-size: 1.55em;
  }

  h3 {
    font-size: 1.15em;
  }
}

@media (min-width: 768px) {
  h1 {
    font-size: 2em;
  }

  h2 {
    font-size: 1.75em;
  }

  h3 {
    font-size: 1.25em;
  }
}

p, ul, ol {
  line-height: 1.4;
  margin: 0 0 1rem;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

.container {
  max-width: 1000px!important;
  margin: auto;
}

// Forms
[type=color], [type=date], [type=datetime-local], [type=datetime], [type=email], [type=month], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=time], [type=url], [type=week], textarea {
  display: block;
  box-sizing: border-box;
  width: 100%;
  height: 2.5rem;
  margin: 0 0 1rem;
  padding: 0.5rem;
  border: 1px solid #eee;
  border-radius: 0;
  outline: 0;
  background-color: #fff;
  box-shadow: none;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: $body-font-color;
  // transition: box-shadow 0.5s, border-color 0.25s ease-in-out;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.button {
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 1rem;
  height: 2.5rem;
  padding: 0.5rem 1rem;
  border: 1px solid transparent;
  border-radius: 0;
  transition: background-color 0.25s ease-out, color 0.25s ease-out, border-color 0.25s ease-out;
  font-family: inherit;
  font-size: 1rem;
  -webkit-appearance: none;
  line-height: 1;
  text-align: center;
  cursor: pointer;
  outline: 0;
  background-color: $ops-color-1;
  color: #fff;

  &.secondary {
    background-color: $ops-color-2;
    color: #fff;

    &.clear {
      background-color: #fff;
      color: $ops-color-2;
      border: 1px solid $ops-color-2;

      &:hover{
        background-color: $ops-color-2;
        color: #fff;
      }
    }
  }

  &.black {
    background-color: $body-font-color;
    color: #fff;

    &.clear {
      background-color: #fff;
      color: $body-font-color;
      border: 1px solid $body-font-color;

      &:hover {
        background-color: $body-font-color;
        color: #fff;
      }
    }
  }
}